/* Breadcrumb
------------------------------------------------------------*/

.u-breadcrumb {
    &--top {
        padding: 22px 0 34px;
    }
    &--bottom {
        padding: 55px 0 0;
    }
}

.c-breadcrumb {
    font-weight: 600;
    i {
        margin-right: 5px;
    }
    &__inner {
        line-height: 34px;
        border-top: 1px solid #cccccc;
        border-bottom: 1px solid #cccccc;
    }
    a {
        margin-right: 6px;
    }
}