.c-list-counter{
    ol {
        counter-reset: item;
        font-size:15px;
        ol{
            padding-left: 35px;
            li:before {
                content: counters(item, ".") " ";
                counter-increment: item;
                
              }
        }
      }
      li {
        display: block;
      }
      li:before {
        content: counters(item, ".") ". ";
        counter-increment: item;
        color: #f5989d;
      }
}