/*------------------------------------------------------------
footer
------------------------------------------------------------*/

.c-footer {
    background: #fafafa;
    color: #000;
    text-align: center;
    &top {
        padding: 35px 0;
        &__link {
            text-align: left;
            li {
                line-height: 20px;
                position: relative;
            }
            i {
                font-size: 10px;
                position: absolute;
                top: calc(50% - 4px);
                left: -1px;
            }
            a {
                padding-left: 11px;
            }
            &--1 {
                i {
                    font-size: 6px;
                    top: calc(50% - 3px);
                }
            }
            &--2 {
                a {
                    padding-left: 12px;
                }
            }
            &--3 {
                a {
                    padding-left: 17px;
                }
            }
        }
        &__title {
            text-align: left;
            font-weight: bold;
            border-bottom: 1px solid #ddd;
            padding-bottom: 4px;
            margin-bottom: 4px;
            margin-left: -1px;
        }
    }
    &bottom {
        border-top: 1px solid #ccc;
        padding: 16px 0;
        font-size: 12px;
        font-weight: 500;
        background: #fff;
    }
}