/* layout
------------------------------------------------------------*/
body{
	font-family: $mainFonts;
	font-size: 1.4rem;
	min-width: $baseWidth;
	line-height: 1.5;
	@include breakpoint(sp) {
		min-width: 320px;
	}
	color: #222222;
	font-weight: 500;
}
//-------------------------------------------------------
main{
	// padding: 0px 0 100px;
}

section{
	padding: 60px 0 90px;
}

//-------------------------------------------------------
.l-container{
	width: $baseWidth;
	margin: 0 auto;
}
//-------------------------------------------------------
.c-mainvisual{
	margin-top: -63px;
	padding: 0;
	height: 700px;
	background: url(../img/bg-header.jpg) no-repeat center;
	background-size: cover;
	color: #fff;
	&__inner{
		position: relative;
		top: 50%;
		transform: translate(0%, -50%);
		padding-top: 63px;
		text-align: center;
	}
	h2{
		width: 100%;
		flex-basis: 100%;
		font-size: 8rem;
		font-weight: 400;
	}
	p{
		width: 100%;
		font-size: 2rem;
	}
	.c-btn1{
		margin-top: 50px;
	}
	//==================
	&--subpage{
		height: 300px;
		h2{
			font-size: 5rem;
		}
	}
}
//==================
.box-img{
	position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    overflow: hidden;
    padding-bottom: 66.67%;
	img{
	position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: auto!important;
    height: auto!important;
    min-width: 100%;
    min-height: 100%;
    object-fit: cover;
	}
}
//==================
.row{
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
}
.col-4{
	width: calc(100%/3 - 33.33px);
}
.col-3{
	width: calc(100%/4 - 15px);
	float: left;
}
//==================
.mb-10{
	margin-bottom: 10px!important;
}
.mb-20{
	margin-bottom: 20px!important;
}
.mb-30{
	margin-bottom: 30px!important;
}
.mb-50{
	margin-bottom: 50px!important;
}
.mb-70{
	margin-bottom: 70px!important;
}
//==================
.link{
	&--1{
		color: #1f53fa;
		text-decoration: underline;
	}
	&--2{
		color: #ff0000;
		text-decoration: underline;
	}
	&--3{
		color: #ff9c00;
		text-decoration: underline;
	}
	&--4{
		color: #ff0000;
	}
}
//=================
.bg-fa{
	background: #fafafa;
}
