table
------------------------------------------------------------*/

/*
------------------------------------------------------------*/
.c-table{
        table{
            background: #ccc;
            text-align: center;
            font-size: 14px;
            border-spacing: 1px;
        }
        th{
            background: #acd373;
            color: #fff;
            line-height: 35px;
        }
        th:not(:first-child),td:not(:first-child){
            width: 140px;
        }
        th:first-child,td:first-child{
            width: 64px;
        }
        td{
            background: #fff;
            line-height: 35px;
        }
        // td:first-child:hover,td:hover{
        //     background: $BlueColor;
        // }
	&--1 {    
        td:first-child{
            background: #ece6e6;
        }
        margin-bottom: 50px;
    }
    &--2 {    
        td:first-child{
            background: #d3d7d9;
        }
    }
    &--3{
        table{
            width: 100%;
        }
        th:first-child,td:first-child{
            width: 17.3%;
        }
        td:first-child{
            background: #ebebeb;
        }
    }
}