/*------------------------------------------------------------
Css of home page
------------------------------------------------------------*/

.p-top {
    line-height: 2;
    // margin: 20px 0 ;
    // padding: 30px 0;
    //-------------------------------------------------------
    &1 {
        padding: 0px;
    }
    //-------------------------------------------------------
    &2 {
        padding: 43px 0 60px;
    }
    //-------------------------------------------------------
    &3 {
        padding: 60px 0;
    }
    //-------------------------------------------------------
    &4 {}
}

.c-randompost {
    .box-img {
        padding-bottom: 36%;
    }
    &__info {
        position: absolute;
        top: 61%;
        left: 40px;
    }
    &__title {
        font-weight: 500;
        font-size: 20px;
        line-height: 30px;
        margin-top: 13px;
    }
    span {
        padding-left: 11.6px;
    }
    .c-tag {
        font-weight: bold;
    }
}

.c-listpost {
    padding-bottom: 30px;
    &__item {
        display: flex;
        width: 100%;
        margin-bottom: 30px;
        .box-img {
            width: 350px;
            padding-bottom: 19%;
        }
        .box-info {
            width: calc(100% - 350px);
            padding-left: 40px;
            position: relative;
            .title {
                font-size: 16px;
                font-weight: bold;
                line-height: 18px;
                height: 18px;
                // overflow: hidden;
                margin-bottom: 10px;
            }
            .desc {
                font-size: 14px;
                line-height: 20px;
            }
            .c-btn-2 {
                position: absolute;
                right: 0;
                bottom: 0;
                span {
                    margin-right: 3px;
                    margin-left: 1.3px;
                }
            }
        }
        .c-tag {
            position: absolute;
            top: 0;
            left: 0;
            font-size: 14px;
            padding: 0 21px;
            line-height: 30px;
            font-weight: 400;
        }
    }
    &__viewmore {
        text-align: center;
    }
}

.c-colpost {
    &__item {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        padding-bottom: 10px;
        .col-4 {
            margin-bottom: 30px;
            .box-img {
                padding-bottom: 62%;
            }
            .box-info {
                .title {
                    font-size: 14px;
                    font-weight: 500;
                    line-height: 20px;
                    height: 60px;
                    overflow: hidden;
                    margin-bottom: 15px;
                }
            }
        }
        .c-tag {
            margin-top: 15px;
            margin-bottom: 15px;
            font-size: 14px;
            font-weight: 500;
            padding: 0 22px;
            line-height: 30px;
        }
        .col-6 {
            width: calc(100%/2 - 25px);
            margin-bottom: 32px;
            .box-img {
                padding-bottom: 39%;
            }
            .box-info {
                .title {
                    font-size: 14px;
                    font-weight: 500;
                    line-height: 20px;
                    height: 40px;
                    overflow: hidden;
                    margin-bottom: 15px;
                }
            }
        }
    }
    &__viewmore {
        text-align: right;
    }
    &__viewmorefull {
        text-align: center;
        width: 100%;
        display: flex;
        margin-top: -14px;
    }
}