/*------------------------------------------------------------
Css of link1 page
------------------------------------------------------------*/
.p-link1{
	line-height: 2;
	&__1{
		padding-bottom: 54px;
	}
	.c-breadcrumb{
		i{
			margin-right: 3px;
		}
	}
	.c-anchor-link{
		&__item{
			a{
				line-height: 45px;
				width: 100%;
				display: block;
				background: #00aeef;
				color: #fff;
				text-align: center;
				border-radius: 5px;
				font-size: 16px;
				font-weight: 500;
			}
			i{
				margin-right: 5px;
			}
		}
	}
	.title-anchor{
		font-size: 18px;
		border-bottom: 2px solid #ccc;
		padding-bottom: 4px;
		line-height: 34px;
		margin-bottom: 22px;
	}
	.c-colpost__item{
		margin-top: 12px;
		padding-bottom: 4px;
		.col-3{
			.box-img{
				padding-bottom: 78.67%;
			}
			.box-info{
				margin-bottom: 15px;
				.title{
					font-size: 14px;
					line-height: 20px;
					font-weight: 500;
					height: 60px;
					overflow: hidden;
				}
			}
		}
	}
}