.c-btn1 {
    display: inline-block;
    padding: 10px 50px;
    font-size: 1.6rem;
    border: 2px solid #fff;
    border-radius: 20px;
    line-height: 1;
}

.c-tag {
    display: inline-block;
    padding: 4px 22px;
    font-size: 16px;
    line-height: 27px;
    z-index: 1;
    &--pink {
        background: $PinkTagColor;
    }
    &--green {
        background: $GreenTagColor;
    }
    &--yellow {
        background: $YellowTagColor;
    }
}

.c-btn-2 {
    display: inline-block;
    padding: 10px 52px 10px 54px;
    background: #afafaf;
    color: #fff;
    border-radius: 5px;
    font-size: 15px;
    font-weight: bold;
}

.c-btn-viewmore {
    display: inline-block;
    padding: 13px 95px;
    font-size: 18px;
    font-weight: bold;
    border: 2px solid;
    border-radius: 5px;
    &--1 {
        background: #ffd800;
        color: #fff;
        border-color: #000;
        padding: 14px 95px 12px 94px;
    }
    &--2 {
        background: #f68e56;
        color: #fff;
        border-color: #000;
        padding: 14px 95px 10px 94px;
    }
    &--3 {
        background: $BlueColor;
        color: #fff;
        border-color: #000;
        width: 100%;
        padding: 14px 97px 12px 94px;
    }
}