/*------------------------------------------------------------
Css of link2 page
------------------------------------------------------------*/
.p-link2{
	line-height: 2;
    .c-colpost__viewmorefull{
        margin-top: -10px;
    }
    .c-btn-full{
        padding: 10px 0 50px;
        .c-btn-viewmore__3{
            color: #000;
            font-size: 32px;
            font-weight: bold;
            padding: 0;
            line-height: 76px;
            border-radius: 0;
        }
    }
    .c-title-main{
        margin-bottom: 21px;
    }
    p{
        font-size: 15px;
        font-weight: 500;
    }
    .c-text-color{
        font-size: 16px;
        line-height: 28px;
        margin-bottom: 2px;
        i{
            font-size: 18px;
            margin-right: 7px;
        }
        &--1{
            color: #11638a;
        }
        &--2{
            color: #f5989d;
        } 
    }
    .c-quote{
        border: 1px solid;
        border-radius: 5px;
        padding: 20px 27px;
        p{margin: 0;}
        i:first-child{
            margin-right: 4px;
        }
        i:last-child{
            margin-left: 4px;
        }
        margin-bottom: 35px;
        &--1{
            color: #11638a;
            margin-bottom: 70px;
        }
        &--2{
            color: #f5989d;
            padding: 18px 27px;
        }
    }
    .c-list-counter{
        margin-bottom: 24px;
    }
    
}