.c-title1 {}

.c-title-main {
    font-size: 24px;
    font-weight: bold;
    border-top: 1px;
    border-left: 8px;
    border-right: 1px;
    border-bottom: 1px;
    border-style: solid;
    padding-left: 20px;
    margin-bottom: 30px;
    &--1 {
        background: #fff9da;
        border-color: #ffd800;
    }
    &--2 {
        background: #fff2f2;
        border-color: #f68e56;
    }
    &--3 {
        background: #ebebeb;
        border-color: $BlueColor;
    }
    &--4 {
        background: #bcd5e1;
        color: #11638a;
        border-top: 0px;
        border-right: 0px;
        border-bottom: 0px;
        margin-top: 4px;
    }
    &--5 {
        background: #ffe8e9;
        color: #f5989d;
        border-top: 0px;
        border-right: 0px;
        border-bottom: 0px;
    }
}

.c-title-caption {
    font-size: 16px;
    font-weight: bold;
    line-height: 40px;
    background: #ece6e6;
    padding-left: 14px;
    margin-bottom: 22px;
}